import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Grid from './grid/Grid'

class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <img src="http://www.iconninja.com/ico/128/react-js-react-logo-js-11722.ico" className="App-logo" alt="logo" />
          <h2>Welcome to React</h2>
        </div>

        <Grid/>
      </div>
    );
  }
}

export default App;



