import React, { Component } from 'react';
const ReactDataGrid = require('react-data-grid');

/*function Grid() {
  return (
    <div> 
      Hello World!
    </div>
  );
}*/

const Grid = React.createClass({
  getInitialState() {
    this.createRows();
    this._columns = [
      { key: 'id', name: 'ID' },
      { key: 'title', name: 'Title' },
      { key: 'count', name: 'Count' } ];

    return null;
  },

  createRows() {
    let rows = [];
    for (let i = 1; i < 100000; i++) {
      rows.push({
        id: i,
        title: 'Title ' + i,
        count: i * 100000
      });
    }

    this._rows = rows;
  },

  rowGetter(i) {
    return this._rows[i];
  },
  render() {
    return(
      <div>
        <h2>Sample Grid with 1,00,000 Records</h2>
        <ReactDataGrid
          columns={this._columns}
          rowGetter={this.rowGetter}
          rowsCount={this._rows.length}
          minHeight={500} />
      </div>
    );
  }
})

export default Grid;


